@charset "UTF-8";
// Global Settings
$main-font: "Quicksand", "游ゴシック体", "Yu Gothic", YuGothic, "ヒラギノ角ゴシック Pro", "Hiragino Kaku Gothic Pro", "メイリオ" , Meiryo , Osaka, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif !important;
$imgfolder: 'assets/img/';

@import "color-settings";
.mceContentBody {
	font-family: $main-font;
}
.editor-area {
	.post-meta {
		padding: 1rem;
		border: 1px solid #DDD;
		border-radius: 3px;
		margin: 1rem auto !important;
		list-style: none;
		li {
			position: static !important;
			margin: 0 !important;
			padding: 0 !important;
			&::before {
				background: transparent !important;
				position: static !important;
			}
		}
		.fa {
			margin-right: 1rem !important;
		}
	}
	.bg-line {
		font-weight: 500;
		padding: .2rem;
		background: linear-gradient( transparent 40%, yellow 40% );
	}
	// 外部リンク （ External Link )
	.link-external-block {
		padding: .5rem;
		list-style: none;
		margin: 0 auto !important;
	}
	.link-external {
		display: inline-block;
		position: relative;
		padding-right: 2.2em;
		transition: all .3s;
		font-family: $main-font;
		&::after {
			position: absolute;
			right: 0.5em;
			padding: 0 1px;
			font-family: FontAwesome;
			transform: translateZ(0);
			content: '\f08e';
		}
		&:hover {
			color: $primary-color;
			text-decoration: none;
		}
		&.img {
			padding: 0;
		}
	}
	.special-site-contents {
		list-style: none;
		li {
			margin-bottom: .5rem;
			padding-bottom: .5rem;
			border-bottom: 1px dashed $gainsboro;
			&::before {
				display: inline-block;
				content: '\f105';
				font: {
					family: FontAwesome;
				}
				background-color: $gainsboro;
				color: $blue-gray;
				padding: 0 5px 0 10px;
				margin-right: .8rem;
			}
		}
	}
	// Page.scss || Post.scss 共通
	.no-bullet ul.square {
		list-style-type: square !important;
	}
	ul.logo {
		list-style: none;
		> li {
			width: 100%;
			display: inline-block;
			vertical-align: middle;
			margin-bottom: 1rem !important;
			&::before {
				display: inline-block;
				content: url(#{$imgfolder}favicon30x30.png);
				width: 30px;
				height: 30px;
				margin-right: 1rem;
				vertical-align: middle;
			}
		}
	}
	p {
		line-height: 2.15;
		margin-bottom: 30px;
	}
	a:not(.button) {
		color: $primary-color;
		word-break: break-all;
	}
	h2 {
		background-color: #FFCE9E;
		border-radius: 3px;
		padding: 21px 18px 20px;
		margin: 80px 0 42px;
		&:first-child {
			margin-top: 0;
		}
		i {
			margin-right: 1rem;
		}
	}
	h3 {
		border-left: 5px solid #FFCE9E;
		padding-left: 18px;
		margin: 60px 0 22px;
	}
	h4 {
		border-left: 5px solid $gainsboro;
		padding-left: 18px;
		margin: 20px 0 22px;
	}
	.title-block {
		background-color: #FFCE9E;
		border-radius: 3px;
		padding: 21px 18px 20px;
		margin: 80px 0 42px;
		h2 {
			background-color: none;
			border-radius: 0;
			padding: 0;
			margin: 0 auto;
		}
	}
	.link-page-up {
		display: inline-block;
		position: relative;
		padding-left: 2.2em;
		transition: all .3s;
		font-family: $main-font;
		&::before {
			position: absolute;
			left: 0.5em;
			padding: 0 1px;
			font-family: FontAwesome;
			transform: translateZ(0);
			content: '\f106';
		}
		&:hover {
			color: $primary-color;
			text-decoration: none;
		}
	}
}
